@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif; }

.board {
  width: 100%; }

.title_row {
  width: 60%;
  margin: 0 auto;
  text-align: center;
  background-color: #A6E0FF;
  height: 100px; }

.submit_row {
  width: 60%;
  margin: 0 auto;
  text-align: center;
  background-color: #A6E0FF;
  height: 200px; }

.title {
  color: white;
  text-shadow: 2px 2px black;
  margin: 0 auto; }

.submit {
  color: white;
  background-color: #A6E0FF;
  border: none;
  width: 150px;
  opacity: 1.5; }
  .submit:hover {
    color: black;
    opacity: 0.2; }

.icon {
  font-size: 100px; }

.disable {
  color: red; }

.enable {
  color: green; }

.top {
  margin-top: 25px; }

